import { cva, VariantProps } from "class-variance-authority";

const buttonStyles = cva(
  "text-white pl-4 pr-16 font-ibm-sans text-sm inline-flex items-center",
  {
    variants: {
      intent: {
        primary: "bg-blue-600 hover:bg-blue-700",
        secondary: "bg-neutral-900",
        tertiary: "border border-blue-600",
        danger: "bg-red-600",
        ghost: "",
        "danger-ghost": "",
        "danger-tertiary": "border border-red-600",
      },
      size: {
        small: "h-8",
        medium: "h-10",
        large: "h-12",
        xl: "h-16",
        "2xl": "h-20",
      },
      skeleton: {
        true: "animate-pulse bg-gray-400 text-gray-400",
      },
    },
    compoundVariants: [
      {
        intent: ["ghost", "tertiary"],
        class: "bg-white text-blue-600",
      },
      {
        intent: ["danger-ghost", "danger-tertiary"],
        class: "bg-white text-red-600",
      },
      {
        size: ["xl", "2xl"],
        class: "pt-4 items-baseline",
      },
    ],
    defaultVariants: {
      intent: "primary",
      size: "large",
      skeleton: false,
    },
  }
);

interface ButtonProps
  extends React.ComponentProps<"button">,
    VariantProps<typeof buttonStyles> {}

export const Button: React.FC<ButtonProps> = ({
  intent,
  size,
  skeleton,
  ...props
}) => {
  return (
    <button className={buttonStyles({ intent, size, skeleton })} {...props} />
  );
};
